@import '../../../variables.scss';
@import '../../../mixin.scss';

.modal {
  :global {
    .modal-dialog {
      .modal-content {
        min-height: 358px;
      }
    }

    .modal-body {
      .contentContainer {
        padding-top: 10px;
      }
      .buttonsContainer {
        flex-direction: column;
        margin-top: 28px;
        button {
          width: 100%;
        }
      }
    }
  }
  .claimContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    .logo {
      height: 48px;
    }
    .title {
      margin-top: 16px;
      color: $DarkBlack;
      font-size: 14px;
      line-height: 16.8px;
      font-weight: 400;
    }
    .value {
      font-size: 32px;
      line-height: 38.4px;
      font-weight: 500;
    }
  }
  .buttonItem {
    width: 100%;
    margin-top: 16px;
    &:first-child {
      margin-top:0;
    }
  }
  .link {
    text-decoration: none;
    font-size: 14px;
    line-height: 16.8px;
    font-weight: 500;
    color: $Green;
    margin-top: 20px;
    transition: all .2s ease;
    &:hover {
      transform: translateY(-2px);
    }
  }
}